import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html'
})
export class NavComponent implements OnInit {

  showStyle: any;

  getStyle() {
    if(this.showStyle) {
      return "0";
    } else {
      return "-100%";
    }
  }

  constructor() { }

  ngOnInit() {
  }

}



